.skills_display-cards {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  display: flex;

  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
  
}

.skills_display_cards-container {
  width: 300px;
  height: 400px;

  background-color: #141617;

  flex-direction: column;
  display: flex;
  box-shadow: 1px 1px 5px 1px black !important;

  align-items: center;
  border-radius: 10px;
  box-shadow: 3px 3px 15px 2px black !important;
}

.skills_display-cards diva {
  margin-bottom: 4%;

  margin-right: 2px;
}

.skills-body-align {
  display: flex;
  flex-direction: row;
  height: 100%;
  margin-top: 15vh;
}
.skills-container-header {
  height: 10vh;
  margin-top: 8h;

  text-align: center;
  color: var(--color--30);

  letter-spacing: 4px;
  font-size: var(--textSize-rem-1-5);
  max-lines: 1;
}


.liner-parent-box {
  width: 300px;
  height: 100px;
  margin: 10px;
  background-color: #141617;

  flex-direction: row;
  display: flex;

 

  align-items: center;
  border-radius: 10px;
  box-shadow: 3px 3px 15px 2px black !important;
}

.liner-metadata-minion {
  color: grey;
  font-style: italic;
  font-size: .8rem;

}