.root-body{
    height: 100%;
    width: 100%;
}

.root_align-to-body {
   height: 100%;
   width: 100%;

   display: flex;
   align-items: center;
   justify-content: center;
 
   
 
   display: flex;
 
   flex-flow: row wrap;
   justify-content: center;
   align-content: center;

}

.liner-parent-box {
    width: 300px;
    height: 100px;
    margin: 10px;
    background-color: #141617;
  
    flex-direction: row;
    display: flex;

   
  
    align-items: center;
    border-radius: 10px;
    box-shadow: 3px 3px 15px 2px black !important;
}

.representative {
    height: 15vh;
    margin-top: 8vh;
  
    text-align: center;
  
    letter-spacing: 8px;
    font-size: 3rem;
    max-lines: 1;
  }

.liner-metadata-minion {
    color: grey;
    font-style: italic;
    font-size: .8rem;

}